.sidebar__container {
  background: #020203;
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 90%;
  width: 94px;
}

.sidebar__icons {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  /* margin-top: 10px; */
}

.sidebar__icon {
  width: 64px;
  height: 64px;
  margin-top: 20px;
}

.copyrights {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  margin-bottom: 15px;
}

.copyrights__content {
  transform: rotate(-90deg);
  white-space: nowrap;
  font-size: 12px;
  line-height: 24px;
  color: white;
}
